: '取消',\n            message: '操作取消！',\n            duration: 2000,\n          });\n        });\n    },\n    SKUDialogConfirm() {\n      if (this.editMode == 1) {\n        skuAPI.updateSKU(this.tmpSKU).then(res => {\n          if (res.status == 200) {\n            this.$notify({\n              title: '成功',\n              message: res.data,\n              type: 'success',\n              duration: 2000,\n            });\n            this.getSKUData();\n          }\n        });\n      } else {\n        skuAPI.addSKU(this.tmpSKU).then(res => {\n          if (res.status == 200) {\n            this.$notify({\n              title: '成功',\n              message: res.data,\n              type: 'success',\n              duration: 2000,\n            });\n            this.getSKUData();\n          }\n        });\n      }\n      this.SKUDialogModal = false;\n    },\n  },\n  created() {\n    this.clientHeight = document.documentElement.clientHeight - 200;\n    this.getSKUData();\n  },\n};\n</script>\n\n<style scoped>\n.main-content-wrap {\n  padding: 10px;\n}\n\n.search-bar {\n  width: 100%;\n  text-align: right;\n  padding-bottom: 10px;\n}\n\n.page-wrap {\n  width: 100%;\n  text-align: right;\n  position: relative;\n  top: 5px;\n  padding-right: 10px;\n}\n\n.e-input {\n  width: 270px;\n}\n</style>\n"],"sourceRoot":""}]);

